body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-global {
  background-image: url('https://www.parisperfect.com/blog/wp-content/uploads/2017/11/Shakespeare-and-Company-Kiren.jpg');
  background-image: url('https://images.ctfassets.net/usf1vwtuqyxm/71TNvxhvGUNjPDH8Ljoq2u/7fe072cd543e68676ed97f3e1fedf748/_A7_1384__1_.jpeg');
  /* background-image: url('https://images.unsplash.com/photo-1589998059171-988d887df646?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8b3BlbiUyMGJvb2t8ZW58MHx8MHx8&w=1000&q=80'); */
  /* background-image: url('https://data.europa.eu/sites/default/files/news/2020-08-06-edp_book_club.png'); */
  /* background-image: url('https://cdn.wallpapersafari.com/80/86/dy1HAE.jpg'); */
  /* background-image: url('https://thumbs.dreamstime.com/b/history-etched-old-paper-scroll-feather-quill-compass-30890377.jpg'); */
  background-size: cover;
  background-attachment: fixed;
}

.starButton {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 25px;
  padding: 0;
}
.on {
  color: #ff1111;
}
.off {
  color: gray;
}

.bg-light {
  background-color: #f8f9faee!important;
}